import { Heading, Link as OutLink, SimpleGrid, Stack, Text } from '@chakra-ui/layout'
import React from 'react'
import Project from '../projects/Project'
import { Link } from 'react-router-dom'
import { useColorMode } from '@chakra-ui/color-mode'
import { Button } from '@chakra-ui/button'
import TTWord from './TTWord'

export default function ProjectsStack() {

    let { colorMode, toggleColorMode } = useColorMode();
    let linkColor = colorMode === 'light' ? 'blue' : 'blue.200'

    return (
        <Stack
        direction='column'
        align='center'
        w='100%'
        pt={24}
        spacing={12}
        >
            <Heading>Projects</Heading>
            <SimpleGrid columns={[1, 1, 1, 1, 3]} gap={12}>
            <Project title="Smart Mirror" src='Mirror.png'>
                <Text>It's a smart mirror! About a year ago, I thought it would be really cool to make my own smart mirror from scratch – <TTWord label="Not using libraries when you can is usually a terrible idea. Don't reinvent the wheel unless your new wheel has a measurable improvement on the wheel-making field or, as in this case, you're trying to learn how a wheel works.">software and hardware</TTWord>. So, I decided to see if I could build the mirror for less money and with fewer tools. The answer was yes! The mirror works by using a device that outputs to a <TTWord label="Always try to recycle! Plus, used equipment ends up being cheaper anyway. You win, and the world wins; what more is there to want?">recycled</TTWord> monitor behind the glass. The glass has a one-way mirror film on it to give the mirror effect. I manually cut all of the wood because I didn’t have electric tools, so some of the structure came out a bit uneven. This was my first real introduction to woodworking, and I think <TTWord label="okay, this is a bit of a stretch. there are screws straight into the front of the mirror. but it doesn't look bad (i think)!">it came out good despite that</TTWord>. The software is a web page served by an Angular server and a Node.js backend. The backend also has Spotify integration. A Raspberry Pi works well as the video input, but anything that can display a webpage also works. In the end, the project helped me develop my woodworking and IOT skills, and I ended up with a pretty cool mirror.</Text>
            </Project>
            <Project title="COVID-19 Check In App" src="FDS_DESIGNER_CROP.png">
                <Text>This is a cross-platform app made in <TTWord label="I developed my first production (robotics) app in Android Studio. It sucked. Not only did it suck, it could only suck on Android because it couldn't even run on iOS in order to suck. Cross-platform development tools are very neat and useful for rapidly developed apps, be it robotics or pandemic-response.">Flutter</TTWord> that allows for the delivery of customized forms in health settings. The project started when I was working with a medical research team at the University of Michigan. Due to the current pandemic, the team I worked with asked me to build a COVID-19 focused application to manage appointments. I used Flutter because it provided ease of development for cross-platform applications as well as fast rendering. I first started with a quick proof-of-concept app that I then built into an app with 3 main sections — login, appointments, and check-ins. During check-ins, each answer can progress to the next question, ask another question, or switch to a different question set depending on the data from the backend. Our team has since migrated to using the <TTWord label="Huge props to the team at Michigan for this app. The intergration with student testing has contributed in large way to safety on campus, and the app just works.">ResponsiBlue Health Check-In System.</TTWord></Text>
            </Project>
            <Project title="Onion Connoisseur" src='onion_connoisseur.png'>
                I created this project with my friend as our final project for SI 206 (Data-Oriented Programming) at the University of Michgian. The algorithm uses a neural network to determine wheter an article is satire or not. You can try this application out for yourself <Link to='/onion_connoisseur'><Text as='span' color={linkColor}>here</Text></Link>. Additionally, you can find source code on our <OutLink isExternal href='https://github.com/efea-umich/SI-206-Final'><Text as='span' color={linkColor}>GitHub repository</Text></OutLink> and more information on the project <OutLink isExternal href='https://github.com/efea-umich/SI-206-Final/blob/3b619acd0efdf760ac17080838d3a31ec705c600/Final_Project_Report.pdf'><Text as='span' color={linkColor}> on our project report</Text></OutLink>.
            </Project>
            </SimpleGrid>
            <Link to='/projects'><Button>View more projects here!</Button></Link>
        </Stack>
    )
}
