import { IconButton } from '@chakra-ui/button'
import { QuestionOutlineIcon } from '@chakra-ui/icons'
import { Text } from '@chakra-ui/layout'
import { Popover, PopoverArrow, PopoverBody, PopoverCloseButton, PopoverContent, PopoverHeader, PopoverTrigger } from '@chakra-ui/popover'
import React from 'react'
import TTWord from '../home/TTWord'

export default function ExplainButton() {
    return (
        <Popover>
            <PopoverTrigger>
                <Text color='gray.500' cursor='pointer'>What Is This?</Text>
            </PopoverTrigger>
            <PopoverContent>
                <PopoverArrow />
                <PopoverCloseButton />
                <PopoverHeader as='b'>
                    What is this?
                </PopoverHeader>
                <PopoverBody>
                    <Text as='p'>This is a fun little program I created with my friend for our final project in SI 206 at the University of Michigan.</Text>
                    <br />
                    <Text as='p'>We use a machine learning algorithm to determine if a provided article is likely to be satire. Try it below by pasting an <TTWord label="Please paste the entire article. I have no idea why but the network doesn't work well on parts of articles. I've checked to make sure shorter articles are still categorized correctly, and the problem only occurs with incomplete articles. So just paste the whole article please. [EDIT: This should now be fixed.]">article</TTWord> below!</Text>
                </PopoverBody>
            </PopoverContent>
        </Popover>
    )
}
