import React, { useState } from 'react'
import { Box, Button, Center, CloseButton, Fade, Flex, Heading, Icon, PopoverTrigger, Portal, Slide, Spacer, Stack, Text, Textarea, useColorMode } from '@chakra-ui/react'
import { QuestionOutlineIcon } from '@chakra-ui/icons'
import ExplainButton from './ExplainButton'
import * as tf from '@tensorflow/tfjs'
import { loadLayersModel } from '@tensorflow/tfjs'
import axios from 'axios'
import TTWord from '../home/TTWord'

export default function OnionConnoisseur() {
    let {colorMode, toggleColorMode} = useColorMode();
    const [enteredArticle, setEnteredArticle] = useState('');
    const [loading, setLoading] = useState(false);
    const [result, setResult] = useState(false);
    const [error, setError] = useState(false);
    const [showHerokuMsg, setShowHerokuMsg] = useState(false);
        
    const onTextFieldChange = (e) => {
        setEnteredArticle(e.target.value);
    }

    const predict = () => {
        setLoading(true);
        setError(false);
        setResult(false);
        let herokuTimeout = setTimeout(() => setShowHerokuMsg(true), 10000);
        let formData = new FormData();
        formData.append('article-body', enteredArticle);
        axios.post('https://onion-connisseur.herokuapp.com/', formData).then(res => {
            console.log(res.data)
            setResult(res.data);
        }).catch(() => {
            setError(true);
        }).finally(() => {
            clearTimeout(herokuTimeout);
            setLoading(false);
            setShowHerokuMsg(false);
        });

    }

    return (
        <Center>
            <Stack spacing={8} align='center' w={['90%', '90%', '60%', '50%', '30%']}>
                <Stack justify align='center' direction='column'>
                    <Heading textAlign='center'>The Onion Connoisseur</Heading>
                    <Box ml={2}>
                        <ExplainButton />
                    </Box>
                    </Stack>
                <Textarea placeholder="Enter your article here" w='100%' onChange={onTextFieldChange} minHeight={['35vh', '35vh', '30vh', '30vh', '30vh']}></Textarea>
                {
                    result ? 
                    <Flex justify='center' w='100%' textAlign='center'><Text>Your article has been determined as {result < 0.5 ? <Text as='span' color='green'>real</Text> : <Text as='span' color='red'>opinionated/satirical</Text>} with a score of {result}.</Text></Flex>
                    : ''
                }
                {
                    error ? 
                    <Flex justify='center' textAlign='center'><Text color={colorMode === 'light' ? 'red' : 'red.400'}>An error occured while processing your request. Feel free tell me that my website is broken so that I can fix it.</Text></Flex>
                    : ''
                }
                <Portal>
                    <Slide in={showHerokuMsg} direction='bottom' unmountOnExit>
                        <Stack
                        p={6}
                        pt={3}
                        pb={12}
                        mt="4"
                        bg="blue.500"
                        rounded="md"
                        shadow="md"
                        color='white'
                        align='center'
                        direction='column'
                        >
                            <CloseButton alignSelf='flex-end' onClick={() => setShowHerokuMsg(false)}/>
                            <Stack
                            direction='column'
                            align='center'
                            textAlign='center'
                            spacing={6}
                            w={['100%','100%','100%','100%', '60%']}
                            >
                                <Heading>Taking too long?</Heading>
                                <Text>If this is your first time running the application, you'll very likely get an error after a long wait. This is because the server that I use for this application is a free Heroku dyno, and because of this, it goes to sleep after 30 minutes of inactivity. Once you send the first request, it takes a second to wake up. Just give it a minute and try again. Any subsequent requests should all work swiftly.</Text>
                            </Stack>
                        </Stack>
                    </Slide>
                </Portal>
                <Button w={'60%'} onClick={predict} isDisabled={enteredArticle == ''} isLoading={loading} loadingText={"This will take a second!"}>Is it satire?</Button>

            </Stack>
        </Center>
    )
}
